<template>
  <div id="app">
    <router-view>
      
    </router-view>
  </div>
</template>

<script>

export default {
  name: 'App',
  data(){
    return{
      appid:'',
      noncestr:'',
      signature:'',
      timestamp:''
    }
  },
  mounted(){

    let curl = window.location.href;
    let share = document.getElementById('share');
    var panda = 'http://student.dfth.com'
    
      this.$axios.get(`${panda}/actives/picWeixinConfig`,{
        params:{
        }
      }).then((res)=>{
          if(res.data.status==1){
              let rdata = res.data.msg;
              this.appid=rdata.appid;
              this.noncestr=rdata.noncestr;
              this.signature=rdata.signature;
              this.timestamp=rdata.timestamp;
          }
      }).then(()=>{
          wx.config({
              debug: false, // ,apialertpclogpc
              appId: this.appid, // 
              timestamp: this.timestamp, // 
              nonceStr: this.noncestr, // 
              signature: this.signature,// 
              jsApiList: ['onMenuShareAppMessage','onMenuShareTimeline','startRecord','stopRecord','playVoice','pauseVoice','stopVoice','uploadVoice']
          });
          if(share){
            wx.ready(function(){
              wx.onMenuShareTimeline({
                  title: '-', // 
                  link: 'http://student.dfth.com/market/index.html#/?new', // JS
                  imgUrl: '', // 
                  success: function () {
                  // 
                }
              });

              wx.onMenuShareAppMessage({
                title: '-', // 
                desc: '', // 
                link: 'http://student.dfth.com/market/index.html#/?new', // JS
                imgUrl: '', // 
                type: '', // ,musicvideolinklink
                dataUrl: '', // typemusicvideo
                success: function () {
                // 
                }
              });
          });
          }
      })
    

  }
}
</script>

<style>
  @import '../src/public/styles/core.css';
</style>



